.header {
  width: 100%;
  background-color: rgb(250, 250, 250);
  height: 70px;
  display: block;
}

.inner_header {
  width: 800px;
  background-color: rgb(250, 250, 250);
  display: block;
  margin: 0 auto;
  height: 100%;
}

.name {
  height: 100%;
  display: table;
  float: left;
}

.name h1 {
  color: black;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
}

.nav {
  float: right;
  height: 100%;
}

.div_ref {
  height: 100%;
  display: table;
  padding: 0px 20px;  
}

.div_ref li {
  display: inline-block;
  vertical-align: middle;
  padding: 8px;
}


/* body {
  background-color: rgb(255, 255, 255);
} */


a, a:visited {
  color: black;
}



*{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}


.content_p1 {
    width: 800px;
    background-color: rgb(255, 255, 255);
    display: block;
    margin: 0 auto;
    height: 100%;
    padding: 40px;
    font-size: 50px;
}
.content_p2 {
  width: 800px;
  background-color: rgb(255, 255, 255);
  display: block;
  margin: 0 auto;
  height: 100%;
  padding: 40px;
  font-size: 30px;
}
.content_p3 {
  width: 800px;
  background-color: rgb(255, 255, 255);
  display: block;
  margin: 0 auto;
  height: 100%;
  padding: 40px;
  font-size: 20px;
}

.logo_set {
  margin: 0 auto;
  display: inline-block;
  height: 100%;
  padding: 20px 80px;
}

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor:pointer;
  padding-top: 5px;
}


@media only screen and (max-width: 800px) {
  .inner_header{
    width: 100%;
  }
  .content_p3 {
    width: 100%;
    display: block;
    padding: 40px;
    margin: 0 auto;
    height: 100%;
    font-size: 3vw;
  }
  #p4 {
    width: 100%;
    padding: 40px;
  }
  #p1,#p2, #p3, #one,#two {
    width: 97%;
  }
  .inner_header {
    padding: 0 40px;
  }
  .logo_set {
    padding: 0 10px;
  }


}

